.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

h1{
  color: white;
  background-color: #345B63;
  padding: 10px;
  font-family: Arial;
  display: flex;
  justify-content:center;
  align-items: center;
}

input{
  padding: 10px;
  /* left: 50%; top: 50%;
  transform: translate(-50%, -50%);
  position: absolute; */
  width: 30vw;
  border-style: solid;
  border-width: 2;
  border-color: green;
  border-radius: 5;
  color: green;
}

button{
  padding: 10px;
  background-color:#2F5D62;
  color: white;
  border-style: 'solid';
  border-width: 2;
  border-color: green;
  border-radius: 5;
  width: 10vw;
}

th,td{
  border: 1px solid #ffff;
  text-align: left;
  padding: 8px;
  font-size: 20px;
  width: 10vw;
}

th{
  color: #ffff;
  background-color: #345B63;
}

td{
  background-color: #E8E46E;
}

.container{
  display: flex;
  justify-content:center;
  align-items: center;
  /* padding-bottom: 20px; */
  margin-bottom: 40px;
}

.weather{
  display: flex;
  justify-content:center;
  align-items: center;
}



